import request from '@/utils/request'

// 
export function Create(data) {
  return request({
    url: `/api/workflow/Engine/FlowTask`,
    method: 'post',
    data
  })
}
// 
export function Update(data) {
  return request({
    url: `/api/workflow/Engine/FlowTask/${data.id}`,
    method: 'put',
    data
  })
}